@media (min-width: 980px) {
  /*-----*/
  .custom-bar-chart {
    margin-bottom: 40px;
  }
}
@media (min-width: 768px) and (max-width: 979px) {
  /*-----*/
  .custom-bar-chart {
    margin-bottom: 40px;
  }
  /*chat room*/
}
@media (max-width: 768px) {
  .header {
    position: absolute;
  }
  /*sidebar*/
  #sidebar {
    height: auto;
    overflow: hidden;
    position: absolute;
    width: 100%;
    z-index: 1001;
  }
  /* body container */
  #main-content {
    margin: 0px!important;
    position: none !important;
  }
  #sidebar > ul > li > a > span {
    line-height: 35px;
  }
  #sidebar > ul > li {
    margin: 0 10px 5px 10px;
  }
  #sidebar > ul > li > a {
    height: 35px;
    line-height: 35px;
    padding: 0 10px;
    text-align: left;
  }
  #sidebar > ul > li > a i {
    /*display: none !important;*/
  }
  #sidebar ul > li > a .arrow,
  #sidebar > ul > li > a .arrow.open {
    margin-right: 10px;
    margin-top: 15px;
  }
  #sidebar ul > li.active > a .arrow,
  #sidebar ul > li > a:hover .arrow,
  #sidebar ul > li > a:focus .arrow,
  #sidebar > ul > li.active > a .arrow.open,
  #sidebar > ul > li > a:hover .arrow.open,
  #sidebar > ul > li > a:focus .arrow.open {
    margin-top: 15px;
  }
  #sidebar > ul > li > a,
  #sidebar > ul > li > ul.sub > li {
    width: 100%;
  }
  #sidebar > ul > li > ul.sub > li > a {
    background: transparent !important ;
  }
  /* sidebar */
  #sidebar {
    margin: 0px !important;
  }
  /* sidebar collabler */
  #sidebar .btn-navbar.collapsed .arrow {
    display: none;
  }
  #sidebar .btn-navbar .arrow {
    position: absolute;
    right: 35px;
    width: 0;
    height: 0;
    top: 48px;
    border-bottom: 15px solid #282e36;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
  }
  /*---------*/
  .modal-footer .btn {
    margin-bottom: 0px !important;
  }
  .btn {
    margin-bottom: 5px;
  }
  /* full calendar fix */
  .fc-header-right {
    left: 25px;
    position: absolute;
  }
  .fc-header-left .fc-button {
    margin: 0px !important;
    top: -10px !important;
  }
  .fc-header-right .fc-button {
    margin: 0px !important;
    top: -50px !important;
  }
  .fc-state-active,
  .fc-state-active .fc-button-inner,
  .fc-state-hover,
  .fc-state-hover .fc-button-inner {
    background: none !important;
    color: #FFFFFF !important;
  }
  .fc-state-default,
  .fc-state-default .fc-button-inner {
    background: none !important;
  }
  .fc-button {
    border: none !important;
    margin-right: 2px;
  }
  .fc-view {
    top: 0px !important;
  }
  .fc-button .fc-button-inner {
    margin: 0px !important;
    padding: 2px !important;
    border: none !important;
    margin-right: 2px !important;
    background-color: #fafafa !important;
    background-image: -moz-linear-gradient(top, #fafafa, #efefef) !important;
    background-image: -webkit-gradient(linear, 0 0, 0 100%, from(#fafafa), to(#efefef)) !important;
    background-image: -webkit-linear-gradient(top, #fafafa, #efefef) !important;
    background-image: -o-linear-gradient(top, #fafafa, #efefef) !important;
    background-image: linear-gradient(to bottom, #fafafa, #efefef) !important;
    filter: progid:dximagetransform.microsoft.gradient(startColorstr='#fafafa', endColorstr='#efefef', GradientType=0) !important;
    -webkit-box-shadow: 0 1px 0px rgba(255, 255, 255, 0.8) !important;
    -moz-box-shadow: 0 1px 0px rgba(255, 255, 255, 0.8) !important;
    box-shadow: 0 1px 0px rgba(255, 255, 255, 0.8) !important;
    -webkit-border-radius: 3px !important;
    -moz-border-radius: 3px !important;
    border-radius: 3px !important;
    color: #646464 !important;
    border: 1px solid #ddd !important;
    text-shadow: 0 1px 0px rgba(255, 255, 255, 0.6) !important;
    text-align: center;
  }
  .fc-button.fc-state-disabled .fc-button-inner {
    color: #bcbbbb !important;
  }
  .fc-button.fc-state-active .fc-button-inner {
    background-color: #e5e4e4 !important;
    background-image: -moz-linear-gradient(top, #e5e4e4, #dddcdc) !important;
    background-image: -webkit-gradient(linear, 0 0, 0 100%, from(#e5e4e4), to(#dddcdc)) !important;
    background-image: -webkit-linear-gradient(top, #e5e4e4, #dddcdc) !important;
    background-image: -o-linear-gradient(top, #e5e4e4, #dddcdc) !important;
    background-image: linear-gradient(to bottom, #e5e4e4, #dddcdc) !important;
    filter: progid:dximagetransform.microsoft.gradient(startColorstr='#e5e4e4', endColorstr='#dddcdc', GradientType=0) !important;
  }
  .fc-content {
    margin-top: 50px;
  }
  .fc-header-title h2 {
    line-height: 40px !important;
    font-size: 12px !important;
  }
  .fc-header {
    margin-bottom: 0px !important;
  }
  /*--*/
  /*.chart-position {*/
  /*margin-top: 0px;*/
  /*}*/
  .stepy-titles li {
    margin: 10px 3px;
  }
  /*-----*/
  .custom-bar-chart {
    margin-bottom: 40px;
  }
  /*menu icon plus minus*/
  .dcjq-icon {
    top: 10px;
  }
  ul.sidebar-menu li ul.sub li a {
    padding: 0;
  }
  /*---*/
  .img-responsive {
    width: 100%;
  }
}
@media (max-width: 480px) {
  .notify-row,
  .search,
  .dont-show,
  .inbox-head .sr-input,
  .inbox-head .sr-btn {
    display: none;
  }
  #top_menu .nav > li,
  ul.top-menu > li {
    float: right;
  }
  .hidden-phone {
    display: none !important;
  }
  .chart-position {
    margin-top: 0px;
  }
  .navbar-inverse .navbar-toggle:hover,
  .navbar-inverse .navbar-toggle:focus {
    background-color: #ccc;
    border-color: #ccc ;
  }
}
@media (max-width: 320px) {
  .login-social-link a {
    padding: 15px 17px !important;
  }
  .notify-row,
  .search,
  .dont-show,
  .inbox-head .sr-input,
  .inbox-head .sr-btn {
    display: none;
  }
  #top_menu .nav > li,
  ul.top-menu > li {
    float: right;
  }
  .hidden-phone {
    display: none !important;
  }
  .chart-position {
    margin-top: 0px;
  }
  .lock-wrapper {
    margin: 10% auto;
    max-width: 310px;
  }
  .lock-input {
    width: 82%;
  }
  .cmt-form {
    display: inline-block;
    width: 75%;
  }
}
